body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#card-app{
  height: 100vh;
}
.a-card {
  width: 90%;
  height: 90vh;
  margin: 5vh 5% 10vh 5%;
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  background-color: beige;
  transition: -webkit-transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
  transition: transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
  transition: transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s, -webkit-transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s; 
}

.b-card-container {
  /* width: 90%; */
