#card-container {
  position: relative;
  display: block;
  width: 100vw;
  padding: 0;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  margin: 0 auto;
  overflow: hidden;
  transition: -webkit-transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
  transition: transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
  transition: transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s, -webkit-transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s; 
}
